import Phaser from './lib/phaser.js';
import { preloader } from './scenes/preloader.js';
import { SCENE_KEYS } from './scenes/scene-keys.js';


class Main extends Phaser.Scene
{
    preload ()
    {
     this.load.image('bckground', '')
    }

    create ()
    {
        var div = document.getElementById('config');

        div.style.backgroundColor = 'bckground'
        };

    }

const config = {
    type: Phaser.WEBGL,
    title: 'MAIZE',
    description: '',
    parent: 'gCanvas',
    width: 1280,
    height: 720,
    backgroundColor: 'bckground',
    pixelArt: false,
    scene: [],
    scale: {
        mode: Phaser.Scale.FIT,
        autoCenter: Phaser.Scale.CENTER_BOTH,
    },
}

const game = new Phaser.Game(config)

game.scene.add(SCENE_KEYS.PRELOADER, preloader);
game.scene.start(SCENE_KEYS.PRELOADER);
